var graceChecker = require("@/utils/graceChecker.js");
import interfaces from '@/utils/interfaces.js'
import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			current: 0, //选择登录方式
			get_code: true,
			count: 60,
			// 登录表单请求数据
			fromList: {
				mobile: '', //手机号
				password: '', // 密码
				code: '', // 验证码
				username: '' //用户名
			},
			//定义表单规则
			rules: [{
					name: "username",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请输入手机号或用户名"
				},
				{
					name: "mobile",
					checkType: "phoneno",
					checkRule: "",
					errorMsg: "请输入正确的手机号"
				},
				{
					name: "code",
					checkType: "string",
					checkRule: "6,6",
					errorMsg: "请输入验证码"
				},
				{
					name: "password",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请输入密码"
				},
			]
		}
	},
	methods: {
		checkoutlogin(index) {
			if (index == 0) {
				this.current = 0
			} else {
				this.current = 1
			}
		},
		enterHome(){
			uni.switchTab({
				url:"/pages/index/index"
			})
		},
		//发送验证码
		sendCode() {
			if (this.fromList.mobile !== "") {
				this.get_code = false;
				this.isgetcode = true;
				let interval = setInterval(() => {
					this.count--;
					if (this.count < 1) {
						this.get_code = true;
						this.count = 60;
						clearInterval(interval);
					}
				}, 1000);
			} else {
				uni.showToast({
					title: "请输入手机号",
					icon: "none"
				});
			}
			this.request({
				url: interfaces.getCodeData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						mobile: this.fromList.mobile,
						type: '1'
					}
				},
				success: ((res) => {
					//console.log(res);
				})
			})
		},

		// 登录
		formSubmit: function(e) {
			//进行表单检查
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, this.rules);
			if (!checkRes) {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
			if (checkRes) {
				this.request({
					url: interfaces.getLoignData,
					dataType: "JSON",
					method: 'POST', //请求方式
					data: {
						data: {
							type: this.fromList.mobile ? 'code_login' : 'pass_login',
							//手机号登录
							mobile: this.fromList.mobile,
							code: this.fromList.code, //验证码
							//密码登录
							user_name: this.fromList.username,
							password: this.fromList.password,
							state_code: '86', //国家区号
						}
					},
					success: (res) => {
						console.log(res);
						if(res.code==200){
							this.login(res.data);
							// uni.navigateBack();
							 uni.switchTab({
							 	url: "/pages/personalCenter/personalCenter"
							 })
						}else{
							uni.showToast({
								title: res.message,
								icon: "none"
							});
							return;
						}
						
							// let clientToken = 'DATA ' + btoa(res.data.client.uid + ':' + res.data.client.user_name + ':' + res.data.token);	

							// let userInfo = {
							// 	userId: res.data.client.uid,
							// 	token: clientToken,
							// 	userName: res.data.client.user_name
							// }
							// uni.setStorage({
							// 	key:'Token',
							// 	data: userInfo,
							// 	success: (res) => {
							// 		uni.getStorage({
							// 			key:'Token',
							// 			success: (res) => {
							// 			//console.log(res.data);
							// 			}
							// 		})
							// 	}
							// });
							
							
			
						
					},
					fail: (e) => {
						uni.showModal({
							content: "请求失败，请重试！",
							showCancel: false
						})
					}
				});
			}
			
		},
		
		...mapMutations(['login'])
	}

}
